/* eslint-disable */
const debug = process.env.NODE_ENV !== 'production'

export default [
 // {path: '/', redirect: '/test'},
 {
    path: '/test',  // 转入转态
    component: function (resolve) {
      require.ensure([], function () {
        resolve(require('./views/test'))
      }, 'test')
    }
  },
  {
    path: '/act/summary2016',  // 浪客故事
    component: function (resolve) {
      require.ensure([], function () {
        resolve(require('./views/act/summary2016'))
      }, 'act/summary2016')
    }
  },
  {
    path: '/act/go2RedPack', // 专门为浪客故事做的红包宣传页
    component: function (resolve) {
      require.ensure([], function () {
        resolve(require('./views/act/go2RedPack'))
      }, 'act/go2RedPack')
    }
  },
  { 
    path: '*', redirect: to => {
      if (debug) {
        return '/test'
      }
    }
  }
  ]





