import * as actions from './actions'
import * as getters from './getter'
import * as types from './mutation-type'

const state = {
  gridData: {},
  FirstArr: [],
  TwoArr: [],
  ThreeArr: [],
  isEmpty: false
}
const mutations = {
  [types.WEEK_TOP_LIST] (state, result) {
    state.gridData = result
    if (result.length > 0) {
      let namesSetFirst = new Set([result[0]])
      let arrFirst = Array.from(namesSetFirst)
      state.FirstArr = arrFirst
    } else {
      state.isEmpty = true
    }
    if (result.length > 2) { // 共有3条以上数据(包含3条)
      let namesSet = new Set([result[1], result[2]])
      let arr2 = Array.from(namesSet)
      state.TwoArr = arr2

      let cout = result.length - 3
      let numArr = []
      for (let i = 0; i < cout; i++) {
        numArr.push(result[i + 3])
      }
      let namesSetThree = new Set(numArr)
      let arr2Three = Array.from(namesSetThree)
      state.ThreeArr = arr2Three
    } else if (result.length === 2) { // 共有2条数据
      let namesSet = new Set([result[1]])
      let arr2 = Array.from(namesSet)
      state.TwoArr = arr2
      console.log(state.TwoArr)
    }
  }
}
export default {
  state,
  mutations,
  actions,
  getters
}



