import * as types from './mutation-type'
import * as http from '../../../utils/common'

/**
 * 获取基金夜8点列表
 * @param commit
 * @param params
 */
export const getFundData = ({commit}, params) => {
  http.post({
    url: '/LVBPages/api/ApiActivity/GetFundAnchorData',
    data: params.data,
    succ: function (res) {
      if (res.result === 1) {
        commit(types.FUND_LIST, res.data)
      } else {
        alert(res.message)
      }
    }
  }, params._this.$store.commit)
}



