<template>
  <div class="main">
    <router-view class="root-view"></router-view>
    <div class='loading' v-if='isLoading'>
      <p class='tip-info'>加载中...</p>
    </div>
    <div class="mui-popup-backdrop mui-active" v-if='isLoading'></div>
  </div>
</template>

<script>
  import {IS_IOS} from './utils/useragent'
  export default {
    data () {
      return {
        transitionName: 'slide',
        isIos: false
      }
    },
    mounted: function () {
      this.isIos = IS_IOS
    },
    watch: {
      '$route' (to, from) {
      }
    },
    computed: {
      direction () {
        return this.$store.state.pages.direction
      },
      isLoading () {
        return this.$store.state.pages.isLoading
      }
    }
  }
</script>
<style lang='less' scoped>
  .main{
    width: 100%;
    height: 100%;
  }
  
  .loading {
    position: fixed;
    z-index: 50000;
    width: 240px;
    min-height: 240px;
    top: 360px;
    left: 50%;
    margin-left: -120px;
    text-align: center;
    border-radius: 15px;
    color: #fff;
    background: url(./assets/images/loading.gif) center 50px no-repeat;
    background-size: 80px;
    background-color: rgba(40, 40, 40, .75);

    .tip-info {
      margin-top: 66%;
      font-size: 28px;
      color: #fff;
    }

  }
</style>



