import * as actions from './actions'
import * as getters from './getter'
import * as types from './mutation-type'

const state = {
  dataGrid: {}
}
const mutations = {
  [types.FUND_LIST] (state, result) {
    state.dataGrid = result
  }
}
export default {
  state,
  mutations,
  actions,
  getters
}



